<template>
	<view class="content">
		<!-- ÂïÜÂìÅËΩÆÊí≠Âõæ -->
		<view class="product-swiper">
			<swiper 
				class="swiper" 
				:indicator-dots="true" 
				:autoplay="false"
				indicator-color="rgba(255,255,255,0.6)"
				indicator-active-color="#FF69B4"
			>
				<swiper-item v-for="(image, index) in product.images" :key="index">
					<image :src="image" class="swiper-image" mode="aspectFill"></image>
				</swiper-item>
			</swiper>
		</view>
		
		<!-- ÂïÜÂìÅ‰ø°ÊÅØ -->
		<view class="product-info">
			<view class="price-section">
				<text class="current-price">¬•{{product.price}}</text>
				<text class="original-price" v-if="product.originalPrice">¬•{{product.originalPrice}}</text>
				<text class="discount-tag" v-if="product.originalPrice">{{getDiscount()}}Êäò</text>
			</view>
			<text class="product-name">{{product.name}}</text>
			<text class="product-desc">{{product.description}}</text>
			<view class="product-meta">
				<text class="sales-info">Â∑≤ÂîÆ{{product.sales}}‰ª∂</text>
				<text class="rating-info">‚≠ê{{product.rating}}</text>
				<text class="stock-info">Â∫ìÂ≠ò{{product.stock}}‰ª∂</text>
			</view>
		</view>
		
		<!-- ËßÑÊ†ºÈÄâÊã© -->
		<view class="spec-section" v-if="product.specs">
			<view class="section-title">ËßÑÊ†ºÈÄâÊã©</view>
			<view class="spec-list">
				<view 
					class="spec-item" 
					v-for="(spec, index) in product.specs" 
					:key="index"
					:class="{ active: selectedSpec === spec.value }"
					@click="selectSpec(spec)"
				>
					<text class="spec-text">{{spec.name}}</text>
					<text class="spec-price" v-if="spec.price">+¬•{{spec.price}}</text>
				</view>
			</view>
		</view>
		
		<!-- Êï∞ÈáèÈÄâÊã© -->
		<view class="quantity-section">
			<view class="section-title">Ë¥≠‰π∞Êï∞Èáè</view>
			<view class="quantity-control">
				<button class="quantity-btn" @click="decreaseQuantity" :disabled="quantity <= 1">-</button>
				<text class="quantity-text">{{quantity}}</text>
				<button class="quantity-btn" @click="increaseQuantity" :disabled="quantity >= product.stock">+</button>
			</view>
		</view>
		
		<!-- ÂïÜÂìÅËØ¶ÊÉÖ -->
		<view class="detail-section">
			<view class="section-title">ÂïÜÂìÅËØ¶ÊÉÖ</view>
			<view class="detail-content">
				<text class="detail-text">{{product.detail || 'ÊöÇÊó†ËØ¶ÁªÜ‰ø°ÊÅØ'}}</text>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
		<view class="bottom-bar">
			<view class="left-actions">
				<button class="action-btn cart" @click="addToCart">
					<text class="btn-icon">üõí</text>
					<text class="btn-text">Âä†ÂÖ•Ë¥≠Áâ©ËΩ¶</text>
				</button>
			</view>
			<view class="right-actions">
				<button class="buy-btn" @click="buyNow">Á´ãÂç≥Ë¥≠‰π∞</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				product: {
					id: 1,
					name: 'VRÁúºÈïúÂ•óË£Ö',
					description: 'È´òÊ∏ÖVRÁúºÈïúÔºåÊ≤âÊµ∏Âºè‰ΩìÈ™åÔºåÊîØÊåÅÂ§öÂπ≥Âè∞Ê∏∏ÊàèÂíåÂ∫îÁî®',
					price: 299,
					originalPrice: 399,
					image: '/static/image/day/VR‰ΩìÈ™åÈ¶Ü.jpg',
					images: [
						'/static/image/day/VR‰ΩìÈ™åÈ¶Ü.jpg',
						'/static/componentIndex.png',
						'/static/templateIndex.png'
					],
					category: 'vr',
					sales: 156,
					rating: 4.8,
					stock: 50,
					specs: [
						{ name: 'Ê†áÂáÜÁâà', value: 'standard', price: 0 },
						{ name: 'Ë±™ÂçéÁâà', value: 'premium', price: 50 },
						{ name: '‰∏ì‰∏öÁâà', value: 'pro', price: 100 }
					],
					detail: '‰∫ßÂìÅÁâπÁÇπÔºö\n‚Ä¢ È´òÊ∏ÖÊòæÁ§∫ÔºåÊ≤âÊµ∏Âºè‰ΩìÈ™å\n‚Ä¢ ËΩªÈáèÂåñËÆæËÆ°Ôºå‰Ω©Êà¥ËàíÈÄÇ\n‚Ä¢ ÊîØÊåÅÂ§öÂπ≥Âè∞Ê∏∏ÊàèÂíåÂ∫îÁî®\n‚Ä¢ ÂÜÖÁΩÆÈü≥È¢ëÁ≥ªÁªü\n‚Ä¢ ÂèØË∞ÉËäÇÁû≥Ë∑ùÂíåÁÑ¶Ë∑ù\n\nÂåÖË£ÖÊ∏ÖÂçïÔºö\n‚Ä¢ VRÁúºÈïú‰∏ªÊú∫\n‚Ä¢ ÊâãÊüÑÊéßÂà∂Âô®\n‚Ä¢ ÂÖÖÁîµÁ∫ø\n‚Ä¢ ËØ¥Êòé‰π¶\n‚Ä¢ Ê∏ÖÊ¥ÅÂ∏É'
				},
				selectedSpec: 'standard',
				quantity: 1
			}
		},
		onLoad(options) {
			if (options.product) {
				try {
					const productData = JSON.parse(decodeURIComponent(options.product));
					this.product = { ...this.product, ...productData };
				} catch (e) {
					console.error('Ëß£ÊûêÂïÜÂìÅÊï∞ÊçÆÂ§±Ë¥•:', e);
				}
			}
		},
		methods: {
			// ËÆ°ÁÆóÊäòÊâ£
			getDiscount() {
				if (!this.product.originalPrice) return 10;
				const discount = Math.round((this.product.price / this.product.originalPrice) * 10);
				return discount;
			},
			
			// ÈÄâÊã©ËßÑÊ†º
			selectSpec(spec) {
				this.selectedSpec = spec.value;
			},
			
			// ÂáèÂ∞ëÊï∞Èáè
			decreaseQuantity() {
				if (this.quantity > 1) {
					this.quantity--;
				}
			},
			
			// Â¢ûÂä†Êï∞Èáè
			increaseQuantity() {
				if (this.quantity < this.product.stock) {
					this.quantity++;
				}
			},
			
			// ËÆ°ÁÆóÊÄª‰ª∑
			calculateTotalPrice() {
				const basePrice = this.product.price;
				const selectedSpec = this.product.specs?.find(s => s.value === this.selectedSpec);
				const specPrice = selectedSpec ? selectedSpec.price : 0;
				return (basePrice + specPrice) * this.quantity;
			},
			
			// Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶
			addToCart() {
				const selectedSpec = this.product.specs?.find(s => s.value === this.selectedSpec);
				const cartItem = {
					...this.product,
					selectedSpec: this.selectedSpec,
					specName: selectedSpec ? selectedSpec.name : 'Ê†áÂáÜÁâà',
					quantity: this.quantity,
					totalPrice: this.calculateTotalPrice()
				};
				
				let cart = uni.getStorageSync('cart') || [];
				
				// Ê£ÄÊü•ÊòØÂê¶Â∑≤Â≠òÂú®Áõ∏ÂêåÂïÜÂìÅÂíåËßÑÊ†º
				const existingIndex = cart.findIndex(item => 
					item.id === cartItem.id && item.selectedSpec === cartItem.selectedSpec
				);
				
				if (existingIndex >= 0) {
					cart[existingIndex].quantity += this.quantity;
					cart[existingIndex].totalPrice = cart[existingIndex].price * cart[existingIndex].quantity;
				} else {
					cart.push(cartItem);
				}
				
				uni.setStorageSync('cart', cart);
				
				uni.showToast({
					title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
					icon: 'success'
				});
			},
			
			// Á´ãÂç≥Ë¥≠‰π∞
			buyNow() {
				const selectedSpec = this.product.specs?.find(s => s.value === this.selectedSpec);
				const orderItem = {
					...this.product,
					selectedSpec: this.selectedSpec,
					specName: selectedSpec ? selectedSpec.name : 'Ê†áÂáÜÁâà',
					quantity: this.quantity,
					totalPrice: this.calculateTotalPrice()
				};
				
				uni.navigateTo({
					url: `/pages/shop/checkout?items=${encodeURIComponent(JSON.stringify([orderItem]))}&type=buy_now`
				});
			}
		}
	}
</script>

<style lang="scss">
	.content {
		background-color: #fef8fa; // Êõ¥Ê∑°ÁöÑÊµÖÁ≤âËâ≤ËÉåÊôØ
		min-height: 100vh;
		padding-bottom: 120rpx;
	}
	
	/* ÂïÜÂìÅËΩÆÊí≠Âõæ */
	.product-swiper {
		.swiper {
			height: 500rpx;
			
			.swiper-image {
				width: 100%;
				height: 100%;
			}
		}
	}
	
	/* ÂïÜÂìÅ‰ø°ÊÅØ */
	.product-info {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.price-section {
			display: flex;
			align-items: center;
			gap: 20rpx;
			margin-bottom: 20rpx;
			
			.current-price {
				font-size: 48rpx;
				font-weight: bold;
				color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
			}
			
			.original-price {
				font-size: 28rpx;
				color: #999;
				text-decoration: line-through;
			}
			
			.discount-tag {
				background-color: #ff4757;
				color: #fff;
				font-size: 22rpx;
				padding: 4rpx 12rpx;
				border-radius: 12rpx;
			}
		}
		
		.product-name {
			font-size: 36rpx;
			font-weight: bold;
			color: #333;
			display: block;
			margin-bottom: 16rpx;
		}
		
		.product-desc {
			font-size: 28rpx;
			color: #666;
			display: block;
			margin-bottom: 20rpx;
			line-height: 1.5;
		}
		
		.product-meta {
			display: flex;
			gap: 30rpx;
			
			.sales-info, .rating-info, .stock-info {
				font-size: 24rpx;
				color: #999;
			}
		}
	}
	
	/* ËßÑÊ†ºÈÄâÊã© */
	.spec-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.section-title {
			font-size: 32rpx;
			font-weight: bold;
			color: #333;
			margin-bottom: 20rpx;
		}
		
		.spec-list {
			display: flex;
			flex-wrap: wrap;
			gap: 20rpx;
			
			.spec-item {
				padding: 20rpx 30rpx;
				border: 2rpx solid #f0f0f0;
				border-radius: 12rpx;
				background-color: #fafafa;
				transition: all 0.3s ease;
				
				&.active {
					border-color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					background-color: #fef8fa; // Êõ¥Ê∑°ÁöÑÊµÖÁ≤âËâ≤ËÉåÊôØ
				}
				
				.spec-text {
					font-size: 28rpx;
					color: #333;
					display: block;
					margin-bottom: 8rpx;
				}
				
				.spec-price {
					font-size: 24rpx;
					color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				}
			}
		}
	}
	
	/* Êï∞ÈáèÈÄâÊã© */
	.quantity-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.section-title {
			font-size: 32rpx;
			font-weight: bold;
			color: #333;
			margin-bottom: 20rpx;
		}
		
		.quantity-control {
			display: flex;
			align-items: center;
			gap: 30rpx;
			
			.quantity-btn {
				width: 60rpx;
				height: 60rpx;
				background-color: #f0f0f0;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 32rpx;
				color: #333;
				
				&:disabled {
					background-color: #f5f5f5;
					color: #ccc;
				}
			}
			
			.quantity-text {
				font-size: 32rpx;
				font-weight: bold;
				color: #333;
				min-width: 80rpx;
				text-align: center;
			}
		}
	}
	
	/* ÂïÜÂìÅËØ¶ÊÉÖ */
	.detail-section {
		background-color: #fff;
		padding: 30rpx;
		
		.section-title {
			font-size: 32rpx;
			font-weight: bold;
			color: #333;
			margin-bottom: 20rpx;
		}
		
		.detail-content {
			.detail-text {
				font-size: 28rpx;
				color: #666;
				line-height: 1.8;
				white-space: pre-line;
			}
		}
	}
	
	/* Â∫ïÈÉ®Êìç‰ΩúÊ†è */
	.bottom-bar {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
		padding: 20rpx 30rpx;
		border-top: 1rpx solid #f0f0f0;
		display: flex;
		align-items: center;
		gap: 20rpx;
		
		.left-actions {
			flex: 1;
			
			.action-btn {
				width: 100%;
				height: 80rpx;
				background-color: #fef8fa; // Êõ¥Ê∑°ÁöÑÊµÖÁ≤âËâ≤ËÉåÊôØ
				color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				border: 2rpx solid #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				border-radius: 40rpx;
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 10rpx;
				
				.btn-icon {
					font-size: 32rpx;
				}
				
				.btn-text {
					font-size: 28rpx;
				}
			}
		}
		
		.right-actions {
			flex: 1;
			
			.buy-btn {
				width: 100%;
				height: 80rpx;
				background-color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				color: #fff;
				border-radius: 40rpx;
				font-size: 28rpx;
				font-weight: bold;
			}
		}
	}
</style> 